<template>
  <div>
    <!-- top -->
    <div class="top-header border-bottom fixed-top">
      <div class="top-back">
        <a href="javascript:history.go(-1)">返回</a>
      </div>
      <h2 class=" f32">
        地下城与勇士 / 东北区 / 东北1区
      </h2>
      <div class="top-right" @click="toShare">
        <a href="javascript:void(0)" class="sharelist"></a>
      </div>
    </div>
    <div class="more_message border f36" v-if="shareShow">
      <ul>
        <router-link to="/index.html">
          <li class="list-more border-bottom"><i class="android"></i>消息</li>
        </router-link>
        <li class="list-more border-bottom shareopen" @click="showShare(true)"><i class="sharew"></i>分享</li>
        <router-link to="/index.html">
          <li class="list-more"><i class="home_back"></i>首页</li>
        </router-link>
      </ul>
    </div>
    <share></share>
    <div class="shade2" @click="offShade" v-if="shareShow"></div>
  </div>
</template>

<script>
  import {mapState, mapMutations, mapActions} from  'vuex'
  import share from 'components/common/share'
  export default {
    name: 'Header',
    data () {
      return {
        shareShow: false,
      }
    },
    components: {
      share
    },
    watch: {
      shadeShow(val){
        if (val) this.shareShow = false
      }
    },
    computed: {
      ...mapState('share', [
        'shadeShow'
      ])
    },
    methods: {
      ...mapMutations('share', {
        showShare: 'share/SET_SHADE'
      }),
      toShare(){
        this.shareShow = !this.shareShow;
      },
      offShade(){
        this.shareShow = false;
      }
    }
  }
</script>

<style src="css/common/list-cl.css" scoped></style>
<style scoped>
  .fadeDown-enter-active {
    animation: slideInUp 0.5s;
    -webkit-animation: slideInUp 0.5s;
  }

  .fadeDown-leave-active {
    animation: fadeOutDown 0.5s;
    -webkit-animation: fadeOutDown 0.5s;
  }
</style>



